<template>
  <div>
    <canvas class="canvas" ref="canvas"></canvas>
    <button @click="downloadCanvas">Download Canvas IMG</button>
    <div>
      <span>{{ msg }}</span>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      canvas: null,
      ctx: null,
      paintColor: "black",
      painting: false,
      msg: "",
    };
  },
  mounted() {
    this.canvas = this.$refs.canvas;
    // canvas태그의 넓이, 높이 설정. css(style)과는 다른 개념으로, 태그 속성(Attribute)으로 명시하거나, (inline sample => <canvas width='500' height='500' ></canvas>)
    // 아래와 같이 직접 canvas 태그에 접근해 속성 할당. 설정하지 않으면
    // 기본값인 width 300px, height 150px으로 적용됨.
    //*** css의 width, height 'px값'과 맞춰줘야 그린대로 정확히 그려집니다. (태그의 크기와 속성으로 설정한 크기가 다르면 그만큼 마우스 위치의 괴리가 발생)
    this.canvas.width = 500;
    this.canvas.height = 500;

    // 서명을 할 때 일어날 마우스이벤트를 canvas 태그에 걸기.
    // FOR PC
    this.canvas.addEventListener("mousedown", this.startPaintInPc);
    this.canvas.addEventListener("mousemove", this.paintLineForPc);
    this.canvas.addEventListener("mouseup", this.stopPainting);
    this.canvas.addEventListener("mouseleave", this.stopPainting);
    // FOR MOBILE
    this.canvas.addEventListener("touchstart", this.startPaintInMobile);
    this.canvas.addEventListener("touchmove", this.paintLineForMobile);
    this.canvas.addEventListener("touchend", this.stopPainting);
    this.canvas.addEventListener("touchcancel", this.stopPainting);

    // canvas 태그의 context 설정(서명 사인을 할 때 선의 색상, 굵기등을 조절).
    this.ctx = this.canvas.getContext("2d");
    this.ctx.strokeStyle = this.paintColor; // 선의 색상
    this.ctx.lineWidth = 2.5; // 선의 굵기
  },
  methods: {
    // FOR PC CANVAS EVENT FUNCTIONS
    startPaintInPc() {
      this.painting = true;
    },
    startPaintInMobile(e) {
      const { x, y } = this.getTouchPoint(e);
      this.ctx.beginPath();
      console.log(x, y);
      //   this.ctx.moveTo(x, y);
      this.painting = true;
    },
    paintLineForPc(e) {
      // canvas 태그에서 마우스를 움직일 때 마우스 좌표 가져오기.
      const x = e.offsetX;
      const y = e.offsetY;
      if (!this.painting) {
        // 아직 그리지 않을때 즉, 'mousedown' 이벤트가 일어나지 않았을때
        // 현재 마우스 좌표를 시작점으로 설정함.
        this.ctx.beginPath(); // 마우스 이동 경로 즉, 선의 이동경로의 시작점 초기화
        this.ctx.moveTo(x, y); //초기화된 선의 이동경로로 마우스 좌표 옮김.
      } else {
        // 'mousedown' 이벤트가 일어나서 즉, 사용자가 그리기 시작했을 때
        // 설정된 선의 이동경로의 시작점 부터 현재 마우스의 좌표까지 경로(선)를 만듬.
        this.ctx.lineTo(x, y);
        this.ctx.stroke(); // 위에서 만들어진 시작점~ 현재 마우스의 좌표 경로를 따라서 선을 그림.
      }
    },
    paintLineForMobile(e) {
      // canvas 태그에서 터치 되었을때 터치좌표 구하기.
      const { x, y } = this.getTouchPoint(e);
      console.log(x, y);
    },
    getTouchPoint(e) {
      const touches = e.changedTouches;
      this.msg = JSON.stringify(touches);
      return {
        x: 0,
        y: 0,
      };
    },
    stopPainting() {
      this.painting = false;
    },
    downloadCanvas() {
      // canvas 태그에 그려진 그림을 base64 로 인코딩된 이미지 파일로 변환
      // *** base64로 인코딩된 이미지 파일은 'text'로 되어있으며, img tag등의 src에 넣으면 이미지로 표시됩니다.
      const imgData = this.canvas.toDataURL();

      // 'a' tag 생성
      const link = document.createElement("a");

      // 'a' tag에 'download' 속성 사용 시 'a' tag 에 연결된 href가 파일이라면 해당 링크 click시 파일을 다운로드를 받음.
      // 파일을 다운로드 할 때 다운받을 파일의 이름 설정.
      link.download = "canvas.png";
      // 'a' tag에 연결할 href 설정. data url(base64로 인코딩된 이미지) 을 연결하면, 해당 링크로 이동 시 이미지가 표시됨.
      link.href = imgData;
      link.click();
    },
    canvasToBase64ToBinary() {
      try {
        // 이 try문은 base64로 인코딩된 이미지 파일을 binary로 바꾸어 formData로 쏘는 구문 입니다.
        // 아래 소스와 같이 하면 서버의 I/O이 없어서 좋은 퍼포먼스를 보여주지만, base64로 인코딩된
        // 이미지만 첨부 할 수 있습니다. bse64 이미지가 아닌 일반 파일 같은 경우엔, catch문과 같이 합니다.
        const imgFile = this.canvas.toDataURL(); // canvas 태그에 그려진 그림을 base64 인코딩된 이미지(data url)로 변환

        // base64 인코딩된 이미지는 앞부분의 data Type 부분과 뒷부분의 base64 text 부분이 ','로 나뉘어져 있음.
        // example : data:image/png;base64,iVBORw0KGgoAAAANSUhEUgA....
        const splitBase64Image = imgFile.value.split(",");

        // base64 인코딩된 이미지의 dataType 부분에서 data: 부분을 날림.
        // data:image/png;base64 To image/png;base64
        const dataType = splitBase64Image[0].replace("data:", "");

        // dataType에서 이미지의 encode Type 을 가져옴.
        const encodeType = dataType.split(";")[1];
        // dataType에서 이미지의 extension(확장자)를 가져옴.
        const imageType = dataType.split(";")[0].replace("image/", "");
        console.log("image encoding type => ", encodeType);
        console.log("image image extension => ", imageType);
        // data url 의 즉, base64인코딩된 이미지의 base64 텍스트만 저장할 변수 선언.
        let content = "";

        // 한번에 표시할 수 있는 text의 숫자가 정해져있어서, 이미지 파일용량이 클 수록
        // ','로 쪼개져있다. 그래서 앞의 데이터 부분을 제외하고 splitBase64Image[1]하고 바로 접근하여 다운로드 하거나, formData로 쏘면
        // 이미지 파일이 깨져서 저장 or 송신 되므로, 데이터 부분을 제외한 index 번호 1부터 각 데이터 사이에 ','를 포함하여 'content' 변수에 다시 담아준다.
        for (let index = 1; index < splitBase64Image.length; index++) {
          if (content != "") {
            content += ",";
          }
          content += splitBase64Image[index];
        }
        // from.append(fieldName, Buffer.from(Buffer.from(content, encodeType), "binary"), fileName);
      } catch {
        // base64 이미지가 아닌 일반 파일들의 formData 설정 입니다.
        // node 서버가 받은 파일을 로컬로 떨군 후, 그 파일을 읽어서 formData에 append 합니다.
        // 아래 코드는 서버의 I/O이 발생하여 서버의 퍼포먼스(속도)가 떨어질 수 있습니다.
        // const content = fs.readFileSync("C:\\Temp\\imk_인터페이스_v0.2.xlsx");
        // from.append(fieldName, Buffer.from(Buffer.from(content, encodeType), "binary"), fileName);
      }
    },
    // FOR MOBILE CANVAS EVENT FUNCTIONS
  },
  destroyed() {
    // 컴포넌트가 언로드 될때 canvas 태그에 걸려있던 이벤트들 제거
    // FOR PC
    this.canvas.removeEventListener("mousedown", this.startPaint);
    this.canvas.removeEventListener("mousemove", this.paintLine);
    this.canvas.removeEventListener("mouseup", this.stopPainting);
    this.canvas.removeEventListener("mouseleave", this.stopPainting);

    // FOR MOBILE
    this.canvas.removeEventListener("touchstart", this.startPaint);
    this.canvas.removeEventListener("touchmove", this.paintLine);
    this.canvas.removeEventListener("touchend", this.stopPainting);
    this.canvas.removeEventListener("touchcancel", this.stopPainting);
  },
};
</script>

<style>
.canvas {
  border: 1px solid black;
  width: 500px;
  height: 500px;
}
</style>
